.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto
}

.icon {
    background: 0 0;
    width: 60px;
    height: 60px;
    background-size: 60px auto;
    margin-bottom: 10px;
    background-repeat: no-repeat;
}

@media (max-width:991px) {
    .icon {
        margin-bottom: 5px;
        background-size: 30px auto;
        width: 30px;
        height: 30px
    }
}

