/*Yash changes*/
.header .menu li button{
    border:0;
    background:transparent;
    transition: all .3s ease-in-out !important;
}
*:focus{
    outline:none;
}
html .slick-next, html .slick-prev {
    height: 35px;
    width: 35px;
    background: #302c8b;
    border-radius: 50%;
    left: 0;
    transform: none;
    top: 0;
    bottom: 0;
    margin: auto -40px;
    padding-left: 3px;
}
html .slick-next {
    left: auto;
    right: 0;
    padding-left: 0;
    padding-right: 3px;
}
html .slick-next::before, html .slick-prev::before {
    opacity: 1;
    content: "";
    height: 11px;
    width: 11px;
    display: block;
    transform: rotate(-45deg) skew(-3deg, -3deg);
    margin: auto;
    border-width: 2px 0 0 2px;
    border-style: solid;
    border-color: #fff;
}
html .slick-next::before {
    border-width: 0px 2px 2px 0;
}
html .slick-next:hover, html .slick-prev:hover {
    background: #2c2d2d;
}
.slick-dots li > button {
    width: 12px;
    height: 12px;
    border: 1px solid #bfcae2!important;
    border-radius: 50%;
    padding: 0;
    margin: 0;
}
html .slick-dots li > button:before {
    display: inline-block;
    background: #bfcae2!important;
    border-radius: 50%;
    width: 6px;
    height: 6px;
    top: 3px;
    left: 3px;
    content: "";
    opacity: 1;
    margin: auto;
    font-size: 0;
}
html .slick-dots li.slick-active > button:before {
    background: #1e5ee5!important;
    width: 8px;
    height: 8px;
    top: 4px;
    left: 4px;
    opacity: 1;
    display: block;
}
html .slick-dots li.slick-active > button {
    width: 16px;
    height: 16px;
    top: 2px!important;
    border-color: #1E5EE5 !important;
}
html .slick-dots li > button:hover{
    border-color: #1E5EE5 !important;
}
html .slick-dots li > button:hover:before{
    background: #1e5ee5!important;
}
html .slick-dots li {
    width: auto;
    height: auto;
    margin: 0 2px;
}
